"use client";
import { useEffect, useState } from "react";
import { LineChart, Line, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer } from "recharts";
import dayjs from "dayjs"

interface SP500StockData {
    Date: string;
    Open: number;
    High: number;
    Low: number;
    Close: number;
    Volume: number;
    Ticker: string;
    Year: number;
    Month: number;
    Day: number;
    moving_avg_20: number;
    volatility_20: number;
    moving_avg_50: number;
    volatility_50: number;
    Change: number;
}

interface PerformanceChartProps {
    data: SP500StockData[];
}

export default function PerformanceChart({ data }: PerformanceChartProps) {
    if (data.length === 0) {
        return <div className="p-4 border rounded-lg shadow-lg bg-white">No data available</div>;
    }

    const filteredData = data.filter(stock => stock.Year >= 2023 && stock.Year <= 2024);

    if (filteredData.length === 0) {
        return <div className="p-4 border rounded-lg shadow-lg bg-white">No data available</div>;
    }

    const ticker = filteredData[0].Ticker;

    return (
        <div className="p-4 border rounded-lg shadow-lg bg-white">
            <h2 className="text-xl font-bold mb-4">Stock Performance: {ticker}</h2>
            <Plot
                data={[
                    {
                        x: filteredData.map(stock => stock.Date),
                        y: filteredData.map(stock => stock.Close),
                        type: "scatter",
                        mode: "lines",
                        name: "Close Price",
                        line: { color: "#8884d8", width: 2 },
                    },
                    {
                        x: filteredData.map(stock => stock.Date),
                        y: filteredData.map(stock => stock.moving_avg_20),
                        type: "scatter",
                        mode: "lines",
                        name: "20-Day Moving Avg",
                        line: { color: "#82ca9d", width: 2, dash: "dash" },
                    },
                    {
                        x: filteredData.map(stock => stock.Date),
                        y: filteredData.map(stock => stock.moving_avg_50),
                        type: "scatter",
                        mode: "lines",
                        name: "50-Day Moving Avg",
                        line: { color: "#ff7300", width: 2, dash: "dot" },
                    }
                ]}
                layout={{
                    autosize: true,
                    xaxis: {
                        title: "Date",
                        tickformat: "%Y-%m-%d",
                        showgrid: false,
                    },
                    yaxis: {
                        title: "Stock Price",
                        showgrid: true,
                    },
                    margin: { t: 10, l: 50, r: 10, b: 50 },
                }}
                useResizeHandler
                style={{ width: "100%", height: "400px" }}
            />
        </div>
    );
}
